import { Component } from '@angular/core';
import { NavController, NavParams, LoadingController, AlertController } from 'ionic-angular';
import { TranslateService } from '@ngx-translate/core';
import { Items } from '../../providers';
import { Api } from '../../providers/api/api';

@Component({
  selector: 'page-item-detail',
  templateUrl: 'item-detail.html'
})
export class ItemDetailPage {
  item: any;
  periods: any;
  periodsids: any;
  cansubscribe: any;
  disabledtg: any;
  disabledbtn: any;
  classloading: boolean;
  nb_hours: number;

  constructor(public api: Api, public navCtrl: NavController, navParams: NavParams, public items: Items, public loadingCtrl: LoadingController,
    public alertCtrl:AlertController,
    private translate: TranslateService) {
    this.item = null;
    this.periods = [];
    this.periodsids = [];
    this.cansubscribe = false;
    this.disabledtg = false;
    this.disabledbtn = false;
    this.classloading = false;
    this.nb_hours = null;
    if (navParams.get('item')) {
      this.item = navParams.get('item');
      this.cansubscribe = this.item.can_subscribe || this.item.can_modify;
      this.disabledtg = !this.item.can_modify;
      this.disabledbtn = this.disabledtg;
      if (this.item.nb_hours) {
        this.nb_hours = this.item.nb_hours;
      }
      
      console.log(this.cansubscribe);
      this.loadPeriods();
    } else if (navParams.get('itemid')) {
      this.classloading = true;
      this.loadItem(navParams.get('itemid'));
    }
  }

  ionViewCanEnter(){
    let seq = this.api.post('checktoken', []).share();
    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      if (res.error !== true) {
        return true;
      } else {
        this.navCtrl.setRoot('LoginPage');
      } 
    }, err => {
      this.navCtrl.setRoot('LoginPage');
    });
  }

  loadPeriods() {
    let datessubscription = this.item.dates_subscriptions;

    datessubscription.forEach((value) => {
      this.periodsids.push(value.id_period);
    });

    this.item.dates.forEach((value) => {
      if (this.item.dates_subscriptions.length == 0) {
        value.subscribed = false;
      } else {
        if (this.periodsids.indexOf(value.id_period) !== -1) {
          value.subscribed = true;
         } else {
          value.subscribed = false;
         }
      }
      value.disabledtoggle = '';
      if (this.item.can_modify === false) {
         value.disabledtoggle = 'disabled';
      }
      
      
      this.periods.push(value);
    });
  }

  loadItem(itemid) {
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`,
    });
    loading.present();
    let seq = this.items.querydetail({id: itemid}).share();
    seq.subscribe((res: any) => {
      this.item = res;
      this.cansubscribe = this.item.can_subscribe || this.item.can_modify;
      this.disabledtg = !this.item.can_modify;
      this.disabledbtn = this.disabledtg;
      if (this.item.nb_hours) {
        this.nb_hours = this.item.nb_hours;
      }
      this.loadPeriods();
    }, err => {
    }, () => { loading.dismiss(); });
  }

  togglePeriod(periodid) {
    let position = this.periodsids.indexOf(periodid);
    if (position == -1) {
      this.periodsids.push(periodid);
     } else {
      this.periodsids.splice(position, 1);
     }
  }

  sendSubscription() {
    var data = {'eventid' : this.item.id, 'periods' : this.periodsids};
    if (this.periodsids.length == 0) {
      let confirmAlert = this.alertCtrl.create({
        message: this.translate.instant('WARNINGPERIOD'),
        buttons: [{
          text: this.translate.instant('CLOSE'),
          role: 'cancel'
        }]
      });
      confirmAlert.present();
      return;
    }
    
    let loading = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/svg/bars.svg" width="100%"/>`
    });
    loading.present();
    let seq = this.api.post('api/subscribeevent', data).share();
    seq.subscribe((res: any) => {
      // If the API returned a successful response, mark the user as logged in
      this.navCtrl.pop();
    }, err => {
      console.error('ERROR', err);
    }, () => { loading.dismiss(); });
  }

  isDisabled() {
    return this.item.can_modify === false;
  }

  sendHours() {
    if (this.nb_hours) {
      var data = {'eventid' : this.item.id, 'nb_hours' : this.nb_hours};
    
      let loading = this.loadingCtrl.create({
        spinner: 'hide',
        content: `<img src="assets/svg/bars.svg" width="100%"/>`
      });
      loading.present();
      let seq = this.api.post('api/nbhours', data).share();
      seq.subscribe((res: any) => {
        // If the API returned a successful response, mark the user as logged in
        this.navCtrl.pop();
      }, err => {
        console.error('ERROR', err);
      }, () => { loading.dismiss(); });
      }
  }

}



